import axios from "axios";
const queryString = require("query-string");

import { logout, store } from "@redux";
import { enumClientIdEnum, enumHttpHeader } from "@configs";

const axiosClient = axios.create({
    baseURL: process.env.REACT_APP_API_URL,

    headers: {
        "Content-Type": "application/json",
        "X-Requested-With": "XMLHttpRequest",
    },

    paramsSerializer: (params) => queryString.stringify(params),
});

axiosClient.interceptors.request.use((config) => {
    const token = store.getState().auth.auth?.accessToken;
    config.headers["access-token"] = token;
    config.headers[enumHttpHeader.ClientId] = enumClientIdEnum.USER;
    config.headers[enumHttpHeader.MerchantId] =
        store.getState().auth.merchantId || store.getState().merchant._id;
    return config;
});

axiosClient.interceptors.response.use(
    (response) => {
        if (response && response.data) {
            return response;
        }
        return response;
    },

    async (error: any) => {
        // Handle errors
        if (error.response && error.response.status === 401) {
            //refreshToken
            const refreshTk = store.getState().auth.auth?.refreshToken?._id;
            if (refreshTk) {
                const refreshTokenBody = { refreshTk };
                try {
                    const res = await axios.post("/auth/login-refresh-token", refreshTokenBody);
                    if (res.status === 200) {
                        //         store.dispatch(refreshToken(res.data as IRefreshTokenRes));
                    }
                } catch {
                    store.dispatch(logout());
                }
            } else {
                store.dispatch(logout());
            }
        }
        throw error;
    }
);

export default axiosClient;
