import {
    Avatar,
    Badge,
    Divider,
    Drawer,
    Dropdown,
    Layout,
    List,
    Menu,
    Popconfirm,
    Row,
    Switch,
    Tooltip,
} from "antd";
import { Book, LogOut, Triangle, Home, MessageCircle, Droplet } from "react-feather";
import { useEffect, useState } from "react";
import { Link, useHistory, useLocation } from "react-router-dom";
import { useDispatch } from "react-redux";
import { MenuMode, MenuTheme } from "antd/lib/menu";

import { capitalize, lowercase } from "@utils";
import { StyledHeader, StyledSidebar } from "@components";
import {
    selectApp,
    setBoxed,
    setCollapsed,
    setDarkSidebar,
    setMobileDrawer,
    setSidebarIcons,
    setSidebarPopup,
    useAppSelector,
    setWeakColor,
    setOptionDrawer,
} from "@redux";

const { SubMenu } = Menu;
const { Header, Sider } = Layout;

const rootSubMenuKeys: any[] = [];

const getKey = (name: string, index: number) => {
    const string = `${name}-${index}`;
    const key = string.replace(" ", "-");
    return key.charAt(0).toLowerCase() + key.slice(1);
};

const UserMenu = (
    <Menu>
        <Menu.Item>Settings</Menu.Item>
        <Menu.Item>Profile</Menu.Item>
        <Menu.Item>Notifications</Menu.Item>
    </Menu>
);

interface ISideBarRoute {
    path?: string;
    name: string;
    icon: JSX.Element;
    children?: Array<{
        path: string;
        name: string;
        badge?: {
            value: string;
        };
    }>;
    badge?: {
        value: string;
    };
}

const routes: ISideBarRoute[] = [
    {
        path: "/",
        name: "Home",
        icon: <Home strokeWidth={1} size={16} />,
    },
    {
        name: "Apps",
        icon: <MessageCircle strokeWidth={1} size={16} />,
        children: [
            {
                path: "/apps/calendar",
                name: "Calendar",
            },
            {
                path: "/apps/messages",
                name: "Messages",
            },
            {
                path: "/apps/social",
                name: "Social",
            },
            {
                path: "/apps/chat",
                name: "Chat",
            },
        ],
    },
    {
        path: "/widgets",
        name: "Widgets",
        icon: <Droplet strokeWidth={1} size={16} />,
        badge: {
            value: "5",
        },
    },
];

interface IProps {
    sidebarTheme: MenuTheme;
    sidebarMode: MenuMode;
    sidebarIcons: boolean;
    collapsed: boolean;
}

export const ComponentSidebar = ({
    sidebarTheme,
    sidebarMode,
    sidebarIcons,
    collapsed,
}: IProps) => {
    //hook
    const location = useLocation();
    const history = useHistory();
    const dispatch = useDispatch();
    //redux state
    const app = useAppSelector(selectApp);
    //page state
    const [openKeys, setOpenKeys] = useState<string[]>([]);
    const pathname = location.pathname;

    const badgeTemplate = (badge: { value: string }) => <Badge count={badge.value} />;

    useEffect(() => {
        routes.forEach((route, index) => {
            const isCurrentPath = pathname.indexOf(lowercase(route.name)) > -1;
            const key = getKey(route.name, index);
            rootSubMenuKeys.push(key);
            if (isCurrentPath) setOpenKeys([...openKeys, key]);
        });
    }, []);

    const onOpenChange = (openKeys: string[]) => {
        const latestOpenKey = openKeys.slice(-1);
        if (rootSubMenuKeys.indexOf(latestOpenKey) === -1) {
            setOpenKeys([...latestOpenKey]);
        } else {
            setOpenKeys(latestOpenKey ? [...latestOpenKey] : []);
        }
    };

    const menu = (
        <>
            <Menu
                theme={sidebarTheme}
                className="border-0 scroll-y"
                style={{ flex: 1, height: "100%" }}
                mode={sidebarMode}
                openKeys={openKeys}
                onOpenChange={onOpenChange}
            >
                {routes.map((route, index) => {
                    if (!route.children) {
                        return (
                            <Menu.Item
                                key={getKey(route.name, index)}
                                className={pathname === route.path ? "ant-menu-item-selected" : ""}
                                onClick={() => {
                                    setOpenKeys([getKey(route.name, index)]);
                                    if (app.mobile) dispatch(setMobileDrawer(!app.mobileDrawer));
                                }}
                            >
                                <Link to={route.path || "/"}>
                                    <a>
                                        {sidebarIcons && (
                                            <span className="anticon">{route.icon}</span>
                                        )}
                                        <span className="mr-2">{capitalize(route.name)}</span>
                                        {route.badge && badgeTemplate(route.badge)}
                                    </a>
                                </Link>
                            </Menu.Item>
                        );
                    } else {
                        return (
                            <SubMenu
                                key={getKey(route.name, index)}
                                title={
                                    <span>
                                        {sidebarIcons && (
                                            <span className="anticon">{route.icon}</span>
                                        )}
                                        <span>{capitalize(route.name)}</span>
                                        {route.badge && badgeTemplate(route.badge)}
                                    </span>
                                }
                            >
                                {route.children.map((subitem, index) => (
                                    <Menu.Item
                                        key={getKey(subitem.name, index)}
                                        className={
                                            pathname === subitem.path
                                                ? "ant-menu-item-selected"
                                                : ""
                                        }
                                        onClick={() => {
                                            if (app.mobile)
                                                dispatch(setMobileDrawer(!app.mobileDrawer));
                                        }}
                                    >
                                        <Link to={`${subitem.path || "/"}`}>
                                            <a>
                                                <span className="mr-auto">
                                                    {capitalize(subitem.name)}
                                                </span>
                                                {subitem.badge && badgeTemplate(subitem.badge)}
                                            </a>
                                        </Link>
                                    </Menu.Item>
                                ))}
                            </SubMenu>
                        );
                    }
                })}
            </Menu>

            <Divider
                className={`m-0`}
                style={{
                    display: `${sidebarTheme === "dark" ? "none" : ""}`,
                }}
            />
            <div className={`py-3 px-4 bg-${sidebarTheme}`}>
                <Row type="flex" align="middle" justify="space-around">
                    <Dropdown overlay={UserMenu}>
                        <span>
                            <Badge
                                count={6}
                                overflowCount={5}
                                style={{
                                    color: "rgb(245, 106, 0)",
                                    backgroundColor: "rgb(253, 227, 207)",
                                }}
                            >
                                <Avatar shape="circle" size={40} src="/static/images/avatar.jpg" />
                            </Badge>
                        </span>
                    </Dropdown>
                    {!collapsed && (
                        <>
                            <span className="mr-auto" />
                            <Link to="https://one-readme.fusepx.com">
                                <a
                                    className={`px-3 ${
                                        sidebarTheme === "dark" ? "text-white" : "text-body"
                                    }`}
                                >
                                    <Tooltip title="Help">
                                        <Book size={20} strokeWidth={1} />
                                    </Tooltip>
                                </a>
                            </Link>

                            <Popconfirm
                                placement="top"
                                title="Are you sure you want to sign out?"
                                onConfirm={() => history.push("/signin")}
                                okText="Yes"
                                cancelText="Cancel"
                            >
                                <a
                                    className={`px-3 ${
                                        sidebarTheme === "dark" ? "text-white" : "text-body"
                                    }`}
                                >
                                    <LogOut size={20} strokeWidth={1} />
                                </a>
                            </Popconfirm>
                        </>
                    )}
                </Row>
            </div>
        </>
    );

    return (
        <>
            <StyledSidebar>
                {!app.mobile && (
                    <Sider
                        width={240}
                        className={`bg-${sidebarTheme}`}
                        theme={sidebarTheme}
                        collapsed={collapsed}
                    >
                        {menu}
                    </Sider>
                )}

                <Drawer
                    closable={false}
                    width={240}
                    placement="left"
                    onClose={() => dispatch(setMobileDrawer(!app.mobileDrawer))}
                    visible={app.mobileDrawer}
                    className="chat-drawer"
                >
                    <StyledSidebar>
                        <div
                            style={{
                                overflow: "hidden",
                                flex: "1 1 auto",
                                flexDirection: "column",
                                display: "flex",
                                height: "100vh",
                            }}
                        >
                            <StyledHeader>
                                <Header>
                                    <Link to="/">
                                        <a className="brand">
                                            <Triangle size={24} strokeWidth={1} />
                                            <strong
                                                className="mx-1"
                                                style={{
                                                    display: "inline",
                                                }}
                                            >
                                                Rewarding platform
                                            </strong>
                                        </a>
                                    </Link>
                                </Header>
                            </StyledHeader>
                            {menu}
                        </div>
                    </StyledSidebar>
                </Drawer>

                <Drawer
                    title="Settings"
                    placement="right"
                    closable={true}
                    width={300}
                    onClose={() => dispatch(setOptionDrawer(!app.optionDrawer))}
                    visible={app.optionDrawer}
                >
                    <List.Item
                        actions={[
                            <Switch
                                size="small"
                                checked={app.boxed}
                                onChange={(checked) => dispatch(setBoxed(checked))}
                            />,
                        ]}
                    >
                        <span
                            style={{
                                flex: "1 0",
                            }}
                        >
                            Boxed view
                        </span>
                    </List.Item>
                    <List.Item
                        actions={[
                            <Switch
                                size="small"
                                checked={app.darkSidebar}
                                disabled={app.weakColor}
                                onChange={(checked) => dispatch(setDarkSidebar(checked))}
                            />,
                        ]}
                    >
                        <span
                            style={{
                                flex: "1 0",
                            }}
                        >
                            Dark sidebar menu
                        </span>
                    </List.Item>
                    <List.Item
                        actions={[
                            <Switch
                                size="small"
                                checked={app.sidebarPopup}
                                disabled={app.collapsed}
                                onChange={(checked) => dispatch(setSidebarPopup(checked))}
                            />,
                        ]}
                    >
                        <span
                            style={{
                                flex: "1 0",
                            }}
                        >
                            Popup sub menus
                        </span>
                    </List.Item>
                    <List.Item
                        actions={[
                            <Switch
                                size="small"
                                checked={app.sidebarIcons}
                                disabled={app.collapsed}
                                onChange={(checked) => dispatch(setSidebarIcons(checked))}
                            />,
                        ]}
                    >
                        <span
                            style={{
                                flex: "1 0",
                            }}
                        >
                            Sidebar menu icons
                        </span>
                    </List.Item>
                    <List.Item
                        actions={[
                            <Switch
                                size="small"
                                checked={app.collapsed}
                                onChange={(checked) => dispatch(setCollapsed(checked))}
                            />,
                        ]}
                    >
                        <span
                            style={{
                                flex: "1 0",
                            }}
                        >
                            Collapsed sidebar menu
                        </span>
                    </List.Item>
                    <List.Item
                        actions={[
                            <Switch
                                size="small"
                                checked={app.weakColor}
                                onChange={(checked) => dispatch(setWeakColor(checked))}
                            />,
                        ]}
                    >
                        <span
                            style={{
                                flex: "1 0",
                            }}
                        >
                            Weak colors
                        </span>
                    </List.Item>
                </Drawer>
            </StyledSidebar>
        </>
    );
};
